@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* ------ VARIABLES ------ */
:root {
  --color-bg: #fff;
  --color-bg-variant: #f5f6f7;
  --color-primary: #3b454a;
  --color-primary-variant: #888;
  --color-secondary: #a1c397;
  --color-secondary-variant: #c8e4b2;
  --color-pink: #ffe5e5;
  --color-white: #fff;
  --color-light: #e7e7e7;

  --transition: all 400ms ease;

  --container-width-lg: 60%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: var(--color-bg);
  color: var(--color-primary);
  line-height: 1.7;
}

/* ------ GENERAL STYLES ------ */
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
  letter-spacing: 0.15rem;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 2rem;
  cursor: pointer;
  border: 1px solid var(--color-primary-variant);
  transition: var(--transition);
  letter-spacing: 0.15rem;
}

.btn:hover {
  background: var(--color-light);
  color: var(--color-primary);
  border-color: transparent;
  transition-duration: 300ms;
  transform: scale(1.02);
}

.btn-primary {
  background: var(--color-secondary);
  color: var(--color-white);
  border: transparent;
  letter-spacing: 0.15rem;
}

.btn-primary:hover {
  background: var(--color-secondary-variant);
  color: var(--color-white);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* ------ SELECTION COLOR ------ */
::selection {
  background-color: var(--color-pink);
}

::-moz-selection {
  background-color: var(--color-pink);
}

/* ------ MEDIA QUERIES ( Medium Tablets) ------ */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  section {
    margin-top: 6rem;
  }
}

/* ------ MEDIA QUERIES ( Small Mobible Devices) ------ */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  section > h2 {
    margin-bottom: 2rem;
  }
}
